import { GuitarTabsList } from '../GuitarTabsList/GuitarTabsList';
import { useChord } from '../../context/chordContext';
import { UkulTabsList } from '../UkulTabsList/UkulTabsList';

export const SideRight = () => {
  const { chord, rotationSet, instr } = useChord();
  // console.log(instr);
  const onClick = (e) => {
    e.preventDefault();
    rotationSet(e.target.textContent);
  };
  return (
    <div className="right">
      <button type="button" onClick={onClick}>
        vertical
      </button>
      <button type="button" onClick={onClick}>
        horizontal
      </button>

      {instr === 'guitar' && <GuitarTabsList chord={chord} />}
      {instr === 'ukulele' && <UkulTabsList chord={chord} />}
    </div>
  );
};
