import { useChord } from '../context/chordContext';
import { MainWindow } from './MainWindow/MainWindow';
import { SideLeft } from './SideLeft/SideLeft';
import { SideRight } from './SideRight/SideRight';

import dbGuitar from '../db/guitar.json';
import dbUklele from '../db/ukulele.json';

export const App = () => {
  const { key, suffix, instr, chordSet } = useChord();
  if (key && suffix && instr === 'guitar') {
    const result = dbGuitar.chords[key].find((element, index, array) => element.suffix === suffix);
    chordSet(result);
  } else if (key && suffix && instr === 'ukulele') {
    const result = dbUklele.chords[key].find((element, index, array) => element.suffix === suffix);
    chordSet(result);
  }
  return (
    <MainWindow>
      <SideLeft />
      <SideRight />
    </MainWindow>
  );
};
